<template>
  <div>
    <h2>login</h2>
    <div class="row">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary">
          <h3>Login</h3>
          <div class="card-body">
            <form @submit.prevent="loginUser">
              <div class="form-group">
                <label for="username">Username</label>
                <input type="text" name="username" v-model="username" id="username" class="form-control" placeholder="username">
              </div>

              <div class="form-group">
                <label for="password">Password</label>
                <input type="text" name="password" v-model="password" id="password" placeholder="Password" class="form-control">
              </div>

              <input type="submit" class="btn btn-danger mt-1" value="submit"/>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <router-link to="/register" class="card-link">Need A New Account</router-link>
            </form>
          </div>
        </div>
      </div>
    </div>
    
  </div>
  
</template>

<script>
import { mapActions } from 'vuex';
export default {
    data(){
      return {
        username: "",
        password: ""
      }
    },
    methods:{
      ...mapActions(['login']),
      loginUser(){
        //console.log(this.username, this.password)
        let user = {
          username: this.username,
          password: this.password
        };

        //console.log(user)
        this.login(user)
        .then(res =>{
          console.log(res.data.user)
          if(res.data.success){
            this.$router.push('/profile');
          }
        })
        .catch(err => {
          console.log(err.message)
        })
        
      }
    }
}
</script>

<style>
.card{
  width: 60%;
  border-radius: 0;
}

.btn{
  border-radius: 0;
}

.form-control {
  border-radius: 0;
}

</style>